		@include vertical;
		@include sm {
			height: auto; } }
	&__bg {
		// position: absolute
		// top: 0
		// left: 0
		// right: 0
		// bottom: 0
		position: relative;
		height: 398px;
		background-size: cover;
		background-position: center;
		background-repeat: no-repeat;
		@include sm {
			height: 250px; } }
	&__name {
		position: absolute;
		left: 20px;
		bottom: 20px; }
	&__btn-wrap {
		position: absolute;
		bottom: 20px;
		right: 20px;
		display: flex; }
	&__btn {
		position: relative;
		width: 40px;
		height: 40px;
		color: $white;
		transition: background-color .3s;
		background-color: $blue;
		display: block;
		@include hover {
			background-color: #C8C8C8; } }
	&__inner {
		width: 100%;
		position: relative;
		text-align: left;
		display: inline-block;
		vertical-align: top; }
	&__content {
		padding: 31px 62px 38px;
		@include xs {
			padding: 0 15px; } }
	&__title {
		padding: 40px 62px;
		font-size: 22px;
		font-weight: 300;
		line-height: 1.136;
		text-align: left;
		@include xs {
			padding: 0 15px 31px; } }
	&__text {
		font-size: 17px;
		font-weight: 300;
		max-width: 100%;
		letter-spacing: 0.034em; }
	&__price-wrap {
		display: flex;
		margin-top: 26px;
		@include sm {
			flex-direction: column; } }
	&__price-box {
		display: flex;
		align-items: center;
		font-family: $circe;
		margin-left: 30px;
		@include md {
			margin-left: 20px; }
		@include sm {
			margin-left: 0; } }
	&__price {
		font-size: 30px;
		margin-right: 21px;
		line-height: 1;
		@include md {
			margin-right: 12px; } }
	&__base-price {
		font-size: 12px;
		line-height: 1.42; }
	.tabs-slide {
		margin-top: 5px;
		font-size: 20px; }
	.sm-order2 {
		@include sm {
			margin-top: 20px;
			order: 2; } } }


