[wind-drop-list] {
	transition: opacity .3s, visibility .3s;
	@include md {
		opacity: 0;
		visibility: hidden;
		position: absolute;
		top: 100%;
		left: 0;
		right: 0;
		background-color: #535356;
		z-index: 3;
		padding: 20px; }
	&.is-show {
		@include md {
			opacity: 1;
			visibility: visible; } } }
[wind-drop-parent] {
	@include md {
		height: auto;
		position: relative;
		padding: 0; } }
[wind-drop-trigger] {
	@include md {
		position: relative;
		margin: 0;
		padding: 29px 15px 23px;
		cursor: pointer;
		user-select: none; }
	.icon {
		position: absolute;
		top: 33px;
		right: 32px;
		font-size: 9px;
		color: $white;
		opacity: .5;
		display: none;
		transform: rotate(90deg);
		transition: transform .3s;
		@include md {
			display: block; } }
	&.is-active {
		.icon {
			transform: rotate(-90deg); } } }

