/* HEADER 1*/
.header {
	padding-bottom: 22px;
	background-color: $white;
	@include xs {
		padding-bottom: 15px; }
	&__wrap {
		@include clr;
		@include md {
			width: 100%;
			height: 100%;
			position: fixed;
			top: 60px;
			left: 0;
			display: flex;
			flex-direction: column;
			align-items: center;
			justify-content: flex-start;
			padding: 30px;
			background-color: $white;
			margin-left: -100%;
			transition: transform .5s;
			z-index: 5;
			&.is-active {
				transform: translateX(100%); } } }
	&__left {
		float: left;
		margin-left: 35px;
		@include md {
			margin: 0;
			float: none;
			order: 2;
			width: 100%; } }
	&__right {
		float: right;
		margin-top: 25px;
		@include md {
			margin: 0;
			float: none;
			display: flex;
			align-items: center;
			justify-content: space-between;
			width: 100%; }
		@include sm {
			flex-direction: column; }
		.btn {
			@include md {
				min-width: 100%; } } } }

.logo {
	color: $black;
	vertical-align: top;
	display: inline-block;
	float: left;
	.header & {
		margin-top: 26px;
		@include xs {
			margin-top: 17px; } }
	&_white {
		color: $white; }
	&__text {
		font-family: $yanone;
		@include f(30px,700,1.2);
		letter-spacing: 0.03em;
		@include xs {
			font-size: 24px; } } }
.search {
	margin-top: 38px;
	font-size: 0;
	vertical-align: top;
	display: inline-block;
	position: relative;
	@include md {
		width: 100%;
		margin-top: 13px;
		padding: 15px 20px 12px 43px;
		border: 2px solid #E6E6E6; }
	&__button {
		background-color: transparent;
		vertical-align: top;
		display: inline-block;
		@include md {
			position: absolute;
			top: 14px;
			left: 13px; }
		.icon {
			font-size: 15px; } }
	&__wrap {
		position: absolute;
		display: flex;
		padding: 18px 22px 20px;
		background-color: $blue;
		left: -22px;
		bottom: -123px;
		z-index: -1;
		opacity: 0;
		transition: opacity .3s;
		@include md {
			position: sticky;
			z-index: 1;
			opacity: 1;
			padding: 0; }
		&.is-active {
			opacity: 1;
			z-index: 2; } }
	&__input {
		font-size: 15px;
		font-weight: 300;
		font-family: $yanone;
		z-index: 3;
		padding: 17px 20px 15px;
		width: 253px;
		@include md {
			position: relative;
			width: 100%;
			padding: 0; }
		&:focus {
			@include placeholder {
				font-size: 0; } } }
	&__close {
		background-color: transparent;
		color: $white;
		margin-left: 21px;
		font-size: 14px;
		@include md {
			display: none; } } }

.nav {
	vertical-align: top;
	display: inline-block;
	@include md {
		display: block;
		margin-top: 13px; }
	&:not(:first-child) {
		margin-left: 20px;
		@include md {
			margin-left: 0; } }
	&__list {
		font-size: 0;
		@include md {
			display: flex;
			flex-direction: column;
			margin: 0 -30px; } }
	&__item {
		vertical-align: top;
		display: inline-block;
		&:not(:first-child) {
			margin-left: 6px;
			@include lg {
				margin-left: 2px; }
			@include md {
				margin-left: 0;
				border-top: 1px solid rgba(#707070,.1); } }
		&.is-active {
			.nav__link {
				color: $white;
				background-color: $blue;
				&:hover {
					color: $white; } } } }
	&__link {
		padding: 31px 16px 16px;
		letter-spacing: 0.035em;
		color: $black;
		text-transform: uppercase;
		font-size: 17px;
		transition: color .3s ease-in-out;
		display: block;
		@include lg {
			padding: 31px 10px 16px; }
		@include md {
			padding: 12px 30px 11px; }
		@include hover {
			color: $blue; } } }
.login {
	margin-top: 6px;
	vertical-align: top;
	display: inline-block;
	color: $black;
	@include f(17px,700,1.55);
	@include md {
		order: 2;
		width: calc(50% - 7px);
		margin-top: 0; }
	@include sm {
		margin-top: 13px;
		width: 100%; }
	&__link {
		color: $black;
		letter-spacing: 0.03em;
		text-transform: uppercase;
		transition: color .3s ease-in-out;
		display: block;
		@include hover {
			color: $blue; }
		@include md {
			border: 2px solid #E6E6E6;
			text-align: center;
			padding: 5px 0; }
		@include sm {
			padding: 11px 0 7px; }
		.icon {
			margin: 3px 7px 0 0;
			font-size: 20px;
			vertical-align: top;
			display: inline-block; } } }
.basket {
	margin-top: 6px;
	vertical-align: top;
	display: inline-block;
	color: $black;
	@include f(17px,700,1.55);
	@include md {
		order: 2;
		width: calc(50% - 7px);
		margin-top: 0; }
	@include sm {
		margin-top: 13px;
		width: 100%; }
	&:not(:last-child) {
		margin-right: 47px;
		@include sm {
			margin-right: 0; } }
	&__text {
		position: relative; }
	&__number {
		position: absolute;
		display: flex;
		align-items: center;
		justify-content: center;
		line-height: 1;
		font-size: 12px;
		font-weight: 700;
		z-index: 1;
		color: $white;
		width: 16px;
		height: 16px;
		border-radius: 50%;
		background-color: $blue;
		top: -8px;
		left: -20px;
		// &:after
		// 	content: ""
		// 	position: absolute
		// 	width: 16px
		// 	height: 16px
		// 	border-radius: 50%
		// 	background-color: $blue
		// 	top: 50%
		// 	left: 50%
		// 	transform: translate(-50%, -50%)
 }		// 	z-index: -1

	&__link {
		color: $black;
		letter-spacing: 0.03em;
		text-transform: uppercase;
		transition: color .3s ease-in-out;
		display: block;
		@include hover {
			color: $blue; }
		@include md {
			border: 2px solid #E6E6E6;
			text-align: center;
			padding: 7px 0 3px; }
		@include sm {
			padding: 11px 0 7px; }
		.icon {
			margin: 1px 7px 0 0;
			font-size: 20px;
			vertical-align: top;
			display: inline-block; } } }

.partnership {
	vertical-align: top;
	display: inline-block;
	&:not(:first-child) {
		margin-left: 25px;
		@include md {
			margin: 0;
			width: calc(50% - 7px); }
		@include sm {
			width: 100%; } } }


/*Sandwich*/
.toggle-mnu {
	position: relative;
	float: right;
	width: 32px;
	height: 32px;
	display: none;
	z-index: 100;
	border: none;
	background-color: transparent;
	margin-top: 23px;
	@include md {
		display: block; }
	@include xs {
		margin-top: 14px; }
	&:before {
		content: "";
		position: absolute;
		top: -10px;
		left: -10px;
		right: -10px;
		bottom: -10px; }
	&:focus {
		outline: none; } }

$w: 32px;
$h: 3px;

.sandwich {
	// display: flex
	// flex-direction: column
	// justify-content: space-around
	width: $w;
	// height: $h
	position: absolute;
	left: 0;
	right: 0;
	margin: auto;
	z-index: 200;
	top: 50%;
	transform: translateY(-50%); }
.sw-topper {
	display: block;
	position: relative;
	transform-origin: left top;
	// top: -($h * 2)
	width: $w;
	height: $h;
	background: $gray;
	border: none;
	// border-radius: rem(4px 4px 4px 4px)
	transition: transform 0.5s;
	// transition-delay: 0.2s, 0s
	margin-bottom: 5px;
	@include xs {
		margin-bottom: 4px; } }
.sw-bottom {
	overflow: hidden;
	display: block;
	position: relative;
	&__inner {
		display: block;
		position: relative;
		width: $w;
		height: $h;
		// top: -($h - ($h / 2))
		transform-origin: center center;
		background: $gray;
		border: none;
		// border-radius: rem(4px 4px 4px 4px)
		transition: transform 0.3s ease-in-out, opacity .5s ease-in-out;
		margin-bottom: 5px;
		@include xs {
			margin-bottom: 4px; } } }

.sw-footer {
	display: block;
	position: relative;
	width: $w;
	height: $h;
	// top: $h
	transform-origin: left;
	background: $gray;
	border: none;
	// border-radius: rem(4px 4px 4px 4px)
	transition: transform 0.5s;
 }	// transition-delay: 0.1s

.sandwich.is-active {
	.sw-topper {
		// top: rem(8px)
		transform: translate(0, -3px) rotate(45deg); }
	.sw-bottom__inner {
		opacity: 0;
		// top: 0
		transform: translateX(-105%); }
	.sw-footer {
		transform: translate(0, 3px) rotate(-45deg);
		@include xs {
			transform: translate(0, 5px) rotate(-45deg); } } }
