.topic {
	padding: 56px 0 65px;
	color: rgba($black, .6);
	background-color: #F2F2F2;
	@include sm {
		padding: 28px 0 32px; }
	&_lg {
		padding: 98px 0; }
	&__wrap {
		display: flex;
		justify-content: space-between;
		align-items: center; }
	&__inner {
		&_horizontal {
			display: flex;
			justify-content: space-between;
			align-items: center;
			@include md {
				flex-direction: column; }
			&:after {
				display: none; }
			p {
				max-width: 631px;
				margin-left: 10px; } } }
	&__title {
		margin-bottom: 30px;
		@include sm {
			margin-bottom: 15px; }
		&_wm {
			margin-bottom: 0;
			@include sm {
				margin-bottom: 0; } }
		&_730 {
			max-width: 730px; } }
	&__subtitle {
		font-size: 30px;
		font-weight: 200;
		margin: 7px 0 18px;
		line-height: 1.2;
		color: $black;
		&_sm {
			font-size: 25px;
			font-weight: 300;
			margin: 0; }
		&_xs {
			font-size: 17px;
			font-weight: 300;
			margin: 0; } }
	&__text {
		color: $black;
		.col {
			&:not(:first-child) {
				@include sm {
					margin-top: 15px; } } } }
	&__number {
		font-family: $circe;
		font-weight: 400;
		color: $black; }
	&__date {
		font-size: 25px;
		font-weight: 300;
		font-family: $circe;
		@include sm {
			font-size: 18px; } }
	&__right {
		.up-text {
			margin-bottom: 14px; } } }

