.contacts {
	position: relative;
	&__map {
		width: 100%;
		height: 650px;
		@include sm {
			height: 400px; } }
	&__wrap {
		position: absolute;
		left: 0;
		right: 0;
		top: 0;
		bottom: 0;
 }		// pointer-events: none
	&__inner {
		height: 100%; }
	&__description {
		height: 100%;
		display: flex;
		justify-content: space-between;
		align-items: flex-start;
		@include sm {
			flex-direction: column;
			justify-content: flex-start;
			padding-top: 80px; } }
	&__details {
		display: flex;
		flex-direction: column;
		margin-top: 107px;
		padding: 41px 65px 44px 48px;
		background-color: $white;
		// pointer-events: auto
		@include sm {
			padding: 15px;
			order: 1;
			margin-top: 0; } }
	&__details-item {
		@include sm {
			font-size: 14px;
			padding-left: 19px; }
		.icon {
			@include sm {
				font-size: 10px;
				height: 12px; } }
		&:not(:last-child) {
			margin-bottom: 30px;
			@include sm {
				margin-bottom: 15px; } } }
	&__cities-wrap {
		position: relative;
		// pointer-events: auto
		max-width: 255px;
		@include lg {
			margin-right: 40px; }
		&.is-show {
			.contacts__cities {
				opacity: 1;
				z-index: 2; } } }
	&__cities {
		padding: 31px 30px 20px;
		background-color: $gray;
		max-height: 650px;
		overflow: auto;
		line-height: 1.117;
		font-size: 17px;
		transition: opacity .3s;
		@include md {
			position: absolute;
			opacity: 0;
			z-index: -1;
			top: 50px;
			right: 0;
			left: 0;
			max-height: 350px;
 } }			// width: 255px


	&__city {
		display: block;
		&:not(:last-child) {
			margin-bottom: 13px; } }
	&__link {
		color: $white;
		// font-size: 17px
		font-weight: 200;
		display: block;
		transition: color .3s;
		&.is-active {
			color: lighten($blue,15%); }
		&:hover {
			color: lighten($blue,15%); } } }

.current-city {
	position: relative;
	padding: 15px 30px;
	background-color: $gray;
	line-height: 1.117;
	font-size: 17px;
	cursor: pointer;
	color: $white;
	width: 255px;
	margin-top: 10px;
	display: none;
	@include md {
		display: block; }
	@include sm {
		padding: 10px 15px;
		margin-bottom: 10px;
		width: 172px; }
	&:after {
		position: absolute;
		top: 50%;
		right: 16px;
		margin-top: -3px;
		vertical-align: top;
		width: 0;
		height: 0;
		border-width: 5px 5px 0;
		display: inline-block;
		width: 0;
		height: 0;
		margin-left: .255em;
		vertical-align: .255em;
		content: "";
		border-top: .3em solid $white;
		border-right: .3em solid transparent;
		border-bottom: 0;
		border-left: .3em solid transparent;
		@include sm {
			right: 6px; } } }




.contacts__cities::-webkit-scrollbar {
	width: 5px; }


.contacts__cities::-webkit-scrollbar-track {
	-webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3); }


.contacts__cities::-webkit-scrollbar-thumb {
	background-color: darkgrey;
	outline: 1px solid slategrey; }
